
.slider_block {
	margin-top: 18px;
	padding-right: 20px;
	display: flex;
	align-items: start;
	justify-content: space-between;
	gap:0;
}

.slider_block.images{
	gap: 18px;
}


.diff_btn {
	flex-shrink: 0;
	font-weight: 500;
	color: var(--black);
	transition: color var(--transition);
	cursor: pointer;
}

.slider {
	width: 100%;
	overflow: hidden;
	flex-shrink: 0;
}

.slider_list {
	display: flex;
	align-items: center;
	transition: transform var(--transition);
}
.slider_list.images {
	gap: 20px;
}